import {PipeTransform, Pipe} from "@angular/core";
import {Logger} from "@util/logger";
import {VsanUiUtils} from "@util/vsan-util";
import {PerfStatsObjectInfo} from "@generated/perf-stats-object-info";
import {VsanIscsiTargetConfig} from "@generated/vsan-iscsi-target-config";
import {StorageCompliance} from "@generated/storage-compliance";

@Pipe({
   name: 'complianceStatusLabel'
})
export class ComplianceStatusLabelPipe implements PipeTransform {

   public transform(details: any | PerfStatsObjectInfo | VsanIscsiTargetConfig | StorageCompliance): any {
      if (!details) {
         return VsanUiUtils.getString("vsan.na.label");
      }

      let status: StorageCompliance = null;
      if ((<any> Object).values(StorageCompliance).includes(details)) {
         status = details;
      } else if (details.spbmComplianceResult) {
         status = details.spbmComplianceResult.complianceStatus;
      } else if (details.homeObjectStorageCompliance) {
         status = details.homeObjectStorageCompliance
      }

      if (!status) {
         return VsanUiUtils.getString("vsan.na.label");
      }

      switch (status) {
         case StorageCompliance.compliant:
            return VsanUiUtils.getString("vsan.pbm.complianceStatus.compliant");
         case StorageCompliance.nonCompliant:
            return VsanUiUtils.getString("vsan.pbm.complianceStatus.nonCompliant");
         case StorageCompliance.notApplicable:
            return VsanUiUtils.getString("vsan.pbm.complianceStatus.notApplicable");
         case StorageCompliance.outOfDate:
            return VsanUiUtils.getString("vsan.pbm.complianceStatus.outOfDate");
         case StorageCompliance.unknown:
            return VsanUiUtils.getString("vsan.pbm.complianceStatus.unknown");
         default:
            Logger.warn("Unrecognized compliance status:", status);
            return VsanUiUtils.getString("vsan.na.label");
      }

   }
}
