import {ChangeDetectionStrategy, Component, Input, OnInit} from "@angular/core";
import {AlertThreshold} from "@generated/alert-threshold";
import {CapacityManagementData} from "@generated/capacity-management-data";
import {CapacityReservationConfig} from "@generated/capacity-reservation-config";
import {DatastoreType} from "@generated/datastore-type";
import {ReservationStatus} from "@generated/reservation-status";
import {CapacityReservationUtil} from "@util/capacity-reservation-util";
import {DiskMgmtUtil} from "@util/disk-mgmt.util";
import {MapUtil} from "@util/map-util";
import {VsanUiUtils} from "@util/vsan-util";
import {ServiceUtils} from "../../../services/service.utils";

@Component({
   selector: "capacity-reservation-configuration",
   templateUrl: "capacity-reservation-configuration.html",
   styleUrls: ["capacity-reservation-configuration.scss"],
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CapacityReservationConfigurationComponent implements OnInit {
   CapacityReservationUtil: typeof CapacityReservationUtil = CapacityReservationUtil;

   @Input()
   isSlackSpaceCapacitySupported: boolean;

   @Input()
   isCapacityCustomizableThresholdsSupportedOnVc: boolean;

   @Input()
   capacityManagementData: CapacityManagementData;

   capacityReservationConfig: CapacityReservationConfig;
   availableDatastoreTypes: DatastoreType[] = [];
   private alertThresholdsByDatastore: Map<DatastoreType, AlertThreshold>;

   ngOnInit(): void {
      if (!this.capacityManagementData) {
         return;
      }
      this.capacityReservationConfig = this.capacityManagementData.capacityReservationConfig;
      const generateKey: any = (key: string) => key as DatastoreType;
      if (this.isCapacityCustomizableThresholdsSupportedOnVc) {
         this.assignCapacityThresholds(generateKey);
      }
      this.availableDatastoreTypes = this.getDataStoreTypes(generateKey);
   }

   showDatastoreType(datastoreType: DatastoreType): boolean {
      return this.isVsanDatastore(datastoreType)
            ? this.isSlackSpaceCapacitySupported || this.isCapacityCustomizableThresholdsSupportedOnVc
            : this.isCapacityCustomizableThresholdsSupportedOnVc;
   }

   getDatastoreTypeLabel(datastoreType: DatastoreType): string {
      return DiskMgmtUtil.getLocalizedDatastoreType(datastoreType);
   }

   isVsanDatastore(datastoreType: DatastoreType): boolean {
      return DatastoreType.VSAN === datastoreType;
   }

   getStateLabel(configOption: ReservationStatus): string {
      return ServiceUtils.enabledStateLabel(CapacityReservationUtil.isReservationEnforced(configOption));
   }

   getWarningThresholdValue(datastoreType: DatastoreType): string {
      const alertThreshold: AlertThreshold = this.alertThresholdsByDatastore.get(datastoreType);
      return this.getThresholdLabel(alertThreshold.warningThreshold, alertThreshold.isDefault);
   }

   getErrorThresholdValue(datastoreType: DatastoreType): string {
      const alertThreshold: AlertThreshold = this.alertThresholdsByDatastore.get(datastoreType);
      return this.getThresholdLabel(alertThreshold.errorThreshold, alertThreshold.isDefault);
   }

   private assignCapacityThresholds(generateKey: any): void {
      this.alertThresholdsByDatastore = MapUtil.toMap<DatastoreType, AlertThreshold>(
            this.capacityManagementData.customizableAlertThresholds,
            generateKey);
      this.alertThresholdsByDatastore.forEach((customizedThreshold, datastoreType) => {
         /**
          * If there is a customized threshold, but it has been disabled,
          * then show the default applied one for the corresponding datastore type.
          */
         if (!customizedThreshold.isEnabled) {
            const currentlyAppliedDefaultThreshold: AlertThreshold = this.getDefaultThreshold(datastoreType);
            if (!currentlyAppliedDefaultThreshold) {
               return;
            }
            this.alertThresholdsByDatastore.set(datastoreType, currentlyAppliedDefaultThreshold);
         }
      });
   }

   private getDefaultThreshold(datastoreType: DatastoreType): AlertThreshold {
      const currentlyAppliedDefaultThreshold: AlertThreshold =
            this.capacityManagementData.allDatastoresCapacityData[datastoreType].thresholds;
      if (!currentlyAppliedDefaultThreshold) {
         return null;
      }
      currentlyAppliedDefaultThreshold.isDefault = true;
      return currentlyAppliedDefaultThreshold;
   }

   private getDataStoreTypes(generateKey: any): DatastoreType[] {
      const capacitiesDataMap: Map<DatastoreType, AlertThreshold> = MapUtil.toMap<DatastoreType, AlertThreshold>(
            this.capacityManagementData.allDatastoresCapacityData, generateKey);
      return DiskMgmtUtil.orderDatastoreTypes(Array.from(capacitiesDataMap.keys()));
   }

   private getThresholdLabel(value: number, isDefault: boolean): string {
      // in case of an empty cluster
      if (!value) {
         return VsanUiUtils.getString("vsan.na.label");
      }

      return isDefault
            ? VsanUiUtils.getString("vsan.services.alertThreshold.default.value", value)
            : VsanUiUtils.getString("vsan.services.alertThreshold.value", value);
   }
}
