import {Component, Input, OnInit} from "@angular/core";
import {Icon} from "@util/icon";
import {VsanServiceStatus} from "@generated/vsan-service-status";
import {PerfStatsObjectInfo} from "@generated/perf-stats-object-info";
import {ServiceUtils} from "app/vsan/services/service.utils";
import {Privileges} from "@generated/privileges";
import {NavigationService} from "@navigation/navigation.service";
import {VsanCapabilityData} from "@generated/vsan-capability-data";

@Component({
   selector: "perf-configuration",
   templateUrl: "perf-configuration.html",
   styleUrls: ["perf-configuration.scss"],
})
export class PerfConfigurationComponent implements OnInit {
   ServiceUtils: typeof ServiceUtils = ServiceUtils;
   Icon: typeof Icon = Icon;

   @Input()
   errorMessage: string;

   @Input()
   vcPermissions: Map<string, boolean>;

   @Input()
   status: VsanServiceStatus;

   @Input()
   config: PerfStatsObjectInfo;

   @Input()
   vcCapabilityData: VsanCapabilityData;

   readPoliciesPermission: boolean;

   constructor(private navigationService: NavigationService) {
   }

   ngOnInit(): void {
      this.readPoliciesPermission = this.vcPermissions.get(Privileges.READ_POLICIES());
   }

   onPolicyClicked(storagePolicyId: string): void {
      this.navigationService.navigateToVmStoragePoliciesView(storagePolicyId);
   }
}
