import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {VsanFileServiceIpType} from "@generated/vsan-file-service-ip-type";
import {Icon} from "@util/icon";
import {VsanServiceStatus} from "@generated/vsan-service-status";
import {ServiceUtils} from "app/vsan/services/service.utils";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {VsanVdfsConfig} from "@generated/vsan-vdfs-config";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";
import {ManagedObject} from "@service/managed-object";
import {AlertActionLink} from "@component/validation/alert-action-link";
import {AlertType} from "@component/validation/alert-type";
import {RefreshHandler} from "@service/global-refresh.service";
import {VsanServiceData} from "@generated/vsan-service-data";
import {VsanConfigService} from "@generated/vsan-config-service";
import {AlertActionLinkUtil} from "@component/validation/alert-action-link-util";
import {FileServiceUtil} from "../../../file-services/file-service-util";
import {TaskMonitorService} from "@service/task-monitor-service";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";

@Component({
   selector: "file-service-configuration",
   templateUrl: "file-service-configuration.html",
   styleUrls: ["file-service-configuration.scss"],
})
export class FileServiceConfigurationComponent implements OnInit {
   ServiceUtils: typeof ServiceUtils = ServiceUtils;
   AlertType: typeof AlertType = AlertType;
   Icon: typeof Icon = Icon;

   @LoadingIndicator()
   loading: boolean;

   @Input()
   clusterRef: ManagedObject;

   @Input()
   clusterName: string;

   @Input()
   @ErrorMessage()
   errorMessage: string;

   @Input()
   vcCapabilityData: VsanCapabilityData;

   @Input()
   availableDomains: DomainOrHostData[];

   @Input()
   isStretchedCluster: boolean;

   @Input()
   isPerfServiceEnabled: boolean;

   @Input()
   upgradeFsvmTask: ManagedObject;

   @Input()
   editClusterPermission: boolean;

   @Output()
   fileServiceStatusLoaded: EventEmitter<VsanServiceData> = new EventEmitter<VsanServiceData>();

   status: VsanServiceStatus;
   vdfsConfig: VsanVdfsConfig;
   ipSettings: IpSettingData[];
   fileAnalyticsStatusLabel: string;
   perfWarningActionLink: AlertActionLink;
   affinitySiteSupported: boolean;
   isIpv6: boolean;

   constructor(private navigationService: NavigationService,
         private taskService: TaskMonitorService,
         private configService: VsanConfigService) {
   }

   ngOnInit(): void {
      this.refresh();
   }

   @RefreshHandler
   @Loader()
   async refresh(): Promise<void> {
      const fileServiceData: VsanServiceData = await this.configService.getFileServicesStatus(this.clusterRef);
      this.status = fileServiceData.status;
      this.vdfsConfig = fileServiceData.details as VsanVdfsConfig;
      // this.vdfsConfig === null means the hosts in this cluster are low version, stop here to avoid UI errors
      if (!this.vdfsConfig) {
         return;
      }
      this.affinitySiteSupported = this.vdfsConfig.precheckResult.affinitySiteSupported && this.isStretchedCluster;
      this.ipSettings = FileServiceUtil.getIpSettings(this.vdfsConfig, this.availableDomains);
      this.isIpv6 = this.vdfsConfig.config?.domainConfig?.ipType === VsanFileServiceIpType.V6;
      this.updateFileAnalyticsConfig();
      this.fileServiceStatusLoaded.emit(fileServiceData);
   }

   onPolicyClicked(storagePolicyId: string): void {
      this.navigationService.navigateToVmStoragePoliciesView(storagePolicyId);
   }

   navigateToNetwork(): void {
      this.navigationService.navigateTo(PlatformExtension.NETWORK_SUMMARY, this.vdfsConfig.config.network);
   }

   private updateFileAnalyticsConfig(): void {
      if (this.vdfsConfig && this.vdfsConfig.config) {
         this.fileAnalyticsStatusLabel = ServiceUtils.enabledStateLabel(this.vdfsConfig.config.isFileAnalyticsEnabled);
         if (this.vdfsConfig.config.isFileAnalyticsEnabled && !this.isPerfServiceEnabled) {
            // If file analytics is enabled but performance service is not, then warning should be shown.
            this.perfWarningActionLink = AlertActionLinkUtil.getPerformanceServiceActionLink(
                  {
                     clusterRef: this.clusterRef,
                     clusterName: this.clusterName,
                  },
                  (taskRef: ManagedObject) => this.awaitThenInvoke(this.onPerformanceEnabled, taskRef));
         }
      }
   }

   getVdfsUpgradeStatus(): string {
      if (this.upgradeFsvmTask) {
         return VsanUiUtils.getString("vsan.fileservices.upgrade.inprogress.label");
      }

      if (this.vdfsConfig.precheckResult) {
         return VsanUiUtils.getString("vsan.services.fileservices.upgradeStatus",
               VsanUiUtils.getString("vsan.services.fileservices.lastUpgrade",
                     VsanDateTimeUtils.formatDate(this.vdfsConfig.precheckResult.lastUpgrade)),
               VsanUiUtils.getString("vsan.services.fileservices.ovfFile", this.getPrecheckVersion()),
         );
      }
      return VsanUiUtils.getString("vsan.na.label");
   }

   getOrgUnitLabel(): string {
     return this.vdfsConfig.config?.domainConfig?.adConfig?.organizationalUnit
           || VsanUiUtils.getString("vsan.na.label");
   }

   get showPerfEnableWarning(): boolean {
      return !this.isPerfServiceEnabled && this.vdfsConfig?.config?.isFileAnalyticsEnabled;
   }

   private getPrecheckVersion(): string {
      return this.vdfsConfig.precheckResult.fsvmVersion
            ? this.vdfsConfig.precheckResult.fsvmVersion
            : VsanUiUtils.getString("vsan.na.label");
   }

   private awaitThenInvoke = (fetchMethod: () => Promise<void>, ...taskRefs: ManagedObject[]) => {
      const runningTasks: Promise<any>[] =
            [...taskRefs].map(taskRef => this.taskService.getAwaitResult(taskRef));
      Promise.all(runningTasks)
            .then(() => fetchMethod.call(this))
            .catch(e => this.errorMessage = e);
   };

   private async onPerformanceEnabled(): Promise<void> {
      VsanUiUtils.globalRefresh();
   }
}

export interface IpSettingData {
   ip: string;
   fqdn: string;
   affinitySite: string;
}
