import {ChangeDetectionStrategy, Component, Input} from "@angular/core";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";
import {ManagedObject} from "@service/managed-object";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {ServiceUtils} from "app/vsan/services/service.utils";
import {SpaceEfficiencyService} from "@service/space-efficiency-service";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanCapabilityData} from "@generated/vsan-capability-data";

@Component({
   selector: "data-services-configuration",
   templateUrl: "data-services-configuration.html",
   styleUrls: ["data-services-configuration.scss"],
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DataServicesConfigurationComponent {
   ServiceUtils: typeof ServiceUtils = ServiceUtils;
   VsanDateTimeUtils: typeof VsanDateTimeUtils = VsanDateTimeUtils;

   @Input()
   clusterRef: ManagedObject;

   @Input()
   vsanConfig: VsanConfigSpec;

   @Input()
   clusterCapabilityData: VsanCapabilityData;

   constructor(private spaceEfficiencyService: SpaceEfficiencyService,
         private navigationService: NavigationService) {
   }

   get spaceEfficiencyLabel(): string {
      return this.spaceEfficiencyService.toLabel(this.vsanConfig?.spaceEfficiencyConfig)
            || VsanUiUtils.getString("vsan.loading");
   }

   get dataAtRestEncryption(): string {
      return ServiceUtils.geStatusLabel(this.vsanConfig?.enableDataAtRestEncryption,
            this.clusterCapabilityData.isEncryptionSupported);
   }

   get isKMSClusterAvailable(): boolean {
      return this.vsanConfig?.enableDataAtRestEncryption &&
            !!this.vsanConfig?.kmipClusterId;
   }

   get isDataInTransitRekeyIntervalAvailable(): boolean {
      return this.vsanConfig?.enableDataInTransitEncryption &&
            !!this.vsanConfig?.rekeyInterval;
   }

   get dataInTransitLabel(): string {
      return ServiceUtils.geStatusLabel(this.vsanConfig?.enableDataInTransitEncryption,
            this.clusterCapabilityData?.isDataInTransitEncryptionSupported);
   }

   onKmipLinkClicked(): void {
      this.navigationService.navigateTo(PlatformExtension.VC_KMIPSERVERS, ManagedObject.uid(ManagedObject.vcRoot(this.clusterRef.serverGuid)));
   }
}
