import {Component, Input, OnInit} from "@angular/core";
import {Icon} from "@util/icon";
import {VsanServiceStatus} from "@generated/vsan-service-status";
import {ServiceUtils} from "app/vsan/services/service.utils";
import {NavigationService} from "@navigation/navigation.service";
import {VsanIscsiTargetConfig} from "@generated/vsan-iscsi-target-config";
import {VsanUiUtils} from "@util/vsan-util";
import IscsiTargetAuthType = VsanIscsiTargetConfig.IscsiTargetAuthType;
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";

@Component({
   selector: "iscsi-configuration",
   templateUrl: "iscsi-configuration.html",
   styleUrls: ["iscsi-configuration.scss"],
})
export class IscsiConfigurationComponent {
   ServiceUtils: typeof ServiceUtils = ServiceUtils;
   VsanIscsiUtil: typeof VsanIscsiUtil = VsanIscsiUtil;
   Icon: typeof Icon = Icon;

   @Input()
   errorMessage: string;

   @Input()
   status: VsanServiceStatus;

   @Input()
   config: VsanIscsiTargetConfig;

   constructor(private navigationService: NavigationService) {}

   formatAuthType(type: IscsiTargetAuthType): string {
      if (!type) {
         return VsanUiUtils.getString("vsan.na.label");
      }
      return VsanIscsiUtil.getAuthenticationLabel(type.toString());
   }

   onPolicyClicked(storagePolicyId: string): void {
      this.navigationService.navigateToVmStoragePoliciesView(storagePolicyId);
   }
}
