import {VsanUiUtils} from "@util/vsan-util";
import {VsanServiceStatus} from "@generated/vsan-service-status";
import {Status} from "@component/card/status";

/**
 * Utility methods for services.
 */
export class ServiceUtils {

   static geStatusLabel(option: boolean, supported: boolean): string {
      if (!supported) {
         return VsanUiUtils.getString("vsan.services.not.supported");
      }
      return ServiceUtils.enabledStateLabel(option);
   }

   static enabledStateLabel(option: boolean): string {
      return option ? VsanUiUtils.getString("vsan.services.enabled") :
            VsanUiUtils.getString("vsan.services.disabled");
   }

   static getServiceStatus(serviceStatus: VsanServiceStatus): Status {
      switch (serviceStatus) {
         case VsanServiceStatus.ENABLED:
            return Status.Enabled;
         case VsanServiceStatus.PARTIAL:
            return Status.Partial;
         default:
            return Status.Disabled;
      }
   }

   static isEnabled(status: VsanServiceStatus): boolean {
      return status === VsanServiceStatus.ENABLED;
   }

   static isDisabled(status: VsanServiceStatus): boolean {
      return status === VsanServiceStatus.DISABLED;
   }

   static isSupported(status: VsanServiceStatus): boolean {
      return status !== VsanServiceStatus.NOT_SUPPORTED;
   }

   static getActionTitle(status: VsanServiceStatus): string {
      return VsanUiUtils.getActionTitleByServiceStatus(this.getServiceStatus(status));
   }
}
